export default {
  data() {
    return {
      showDiscuss: false,
      showQuestion: false,
      show: false,
      isAdmin: 0,
      question: "", //随机问题信息
      discussTitle: "", //话题标题
      answerQue: "",
      userInfo: "", //用户信息
      isActive: 1,
      course_id: "", //查询课程id
      creater_id: "", //课程创建者id
      courseDetail: "", //课程详情
      JoinCourseNum: "", //加入课程人数信息
      workList: "", //待发布作业列表
      nowWorkList: "", //已发布作业列表
      workMegs: "", //显示作业信息
      getQuesList: "", //随机问题列表
      getDiscussList: "" // 获取讨论话题列表
    };
  },
  onShow() {
    let t = this;
    if (t.isAdmin == 1) {
      t.getTitle(1);
    } else {
      t.getTitle(2);
    }
  },
  onLoad(options) {
    console.log(options);
    let t = this;
    t.userInfo = uni.getStorageSync("userInfo");
    let userInfo = t.userInfo;
    t.creater_id = options.user_id;
    t.course_id = options.id;
    if (userInfo.id == t.creater_id) {
      t.isAdmin = 1;
    }
    t.getCourseDetail();
    t.getWorkList();
    t.getQues();
    t.getDisList();
  },
  methods: {
    toCheckMegs(workMegs) {
      let t = this;
      t.show = true;
      t.workMegs = workMegs;
    },
    toShowWork(id, isShow) {
      let t = this;
      if (isShow) {
        t.$utils.showToast("作业已发布");
        return;
      }
      let data = {
        id: id
      };
      t.$utils.ajax(t.$api.toShowWork, "post", data, res => {
        t.$utils.showToast(res.msg);
        t.getWorkList();
      });
    },
    toShowDis(id, isShow) {
      let t = this;
      console.log(id, "id");
      console.log(isShow, "isShow");
      if (isShow) {
        t.$utils.showToast("作业已发布");
        return;
      }
      let data = {
        id: id
      };
      t.$utils.ajax(t.$api.toShowDis, "post", data, res => {
        t.$utils.showToast(res.msg);
        console.log(1111);

        t.getDisList();
      });
    },

    getWorkList() {
      let t = this;
      let data = {
        course_id: t.course_id
      };
      t.$utils.ajax(t.$api.getWorkList, "get", data, res => {
        console.log(res, "1111");
        t.workList = res;
      });
    },
    getNewWorkList() {
      let t = this;
      let data = {
        isShow: 1,
        course_id: t.course_id
      };
      t.$utils.ajax(t.$api.getWorkList, "get", data, res => {
        console.log(res, "1111");
        t.nowWorkList = res;
      });
    },
    getTitle(isActive) {
      let t = this;
      isActive == 1
        ? (t.isActive = 1)
        : isActive == 2
        ? (t.isActive = 2)
        : isActive == 3
        ? (t.isActive = 3)
        : (t.isActive = 4);
      if (isActive == 1) {
        let data = {
          id: t.course_id
        };
        t.$utils.ajax(t.$api.getJoinCourseNum, "get", data, res => {
          console.log(res, "听课成员");
          t.JoinCourseNum = res;
        });
      }
    },
    getCourseDetail() {
      let t = this;
      let data = {
        id: t.course_id
      };
      t.$utils.ajax(t.$api.getCourseList, "get", data, res => {
        console.log(res, "课程详情");
        t.courseDetail = res;
      });
    },
    getQues() {
      let t = this;
      let data = {
        course_id: t.course_id
      };
      t.$utils.ajax(t.$api.getQuesList, "get", data, res => {
        t.getQuesList = res;
        console.log(res, "随机问题列表");
      });
    },
    comfirm() {
      let t = this,
        JoinCourseNum = t.JoinCourseNum;
      if (!t.question) {
        t.$utils.showToast("请输入随机问题信息");
        return;
      }
      let length = JoinCourseNum.length;
      let Number = Math.floor(Math.random() * length);
      let StudentMegs = JoinCourseNum[Number];
      console.log(StudentMegs, "随机提问人员信息");
      let data = {
        course_id: t.course_id,
        user_id: StudentMegs.user_id,
        questionMegs: t.question,
        studentName: StudentMegs.studentName
      };
      t.$utils.ajax(t.$api.toAddQuestions, "post", data, res => {
        t.$utils.showToast(res.msg);
        t.getQues();
      });
    },
    discussComfirm() {
      let t = this;
      let data = {
        isActive: 2,
        course_id: t.course_id,
        creater_id: t.creater_id,
        title: t.discussTitle
      };
      t.$utils.ajax(t.$api.toAddDiscuss, "post", data, res => {
        t.$utils.showToast(res.msg);
        if (res.flag == "yes") {
          t.showDiscuss = false;
        }
        t.getDisList();
      });
    },
    getDisList() {
      let t = this;
      let data = {
        course_id: t.course_id
      };
      t.$utils.ajax(t.$api.getDiscussList, "get", data, res => {
        console.log(res, "讨论区话题列表");
        t.getDiscussList = res;
      });
    },

    toAnswer(id) {
      let t = this;
      if (t.userInfo.id != id) {
        t.$utils.showToast("您暂无权回答此问题");
      } else {
        console.log("我来回答");
      }
    },
    toDiscuss(id, courseid, title) {
      let t = this,
        arr = t.JoinCourseNum;
      let a = arr.some(v => {
        if (v.user_id == t.userInfo.id) {
          return true;
        }
      });
      if (!a) {
        console.log("欢迎参加讨论！");
        uni.navigateTo({
          url:
            "./answerDiscuss?discuss_id=" +
            id +
            "&&course_id=" +
            courseid +
            "&&title=" +
            title
        });
      } else {
        t.$utils.showToast("您暂无权参与此话题");
      }
    },
    toDelect(user_id) {
      let t = this;
      console.log(user_id);
      let data = {
        student_id: user_id
      };
      t.$utils.ajax(t.$api.toDelectStudent, "get", data, res => {
        t.$utils.showToast(res.msg);
        t.getTitle(1);
      });
    },
    toAddWork(creater, course_id) {
      let t = this;
      console.log(creater);
      console.log(course_id);
      uni.navigateTo({
        url: "./createWork?creater=" + creater + "&&course_id=" + course_id
      });
    }
  }
};
